.category {
  grid-column: 1/-1;
  display: flex;
  flex-direction: column;
  gap: 32px;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 32px;

    @include on-tablet {
      flex-direction: row;
      gap: 16px;
    }

    @include on-desktop {
      flex-direction: row;
      gap: 16px;
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
  }
  &__image-frame {
    aspect-ratio: 1/1;
    display: flex;
    flex-direction: column;
    gap: 24px;
  }


}
