.topbar {
  display: flex;
  gap: 24px;
  flex-direction: row;
  box-shadow: 0 1px 0 0 #e2e6e9;

  &__logo-container {
    display: flex;
    padding-inline: 16px;
    width: 96px;
  }

  &__nav-container {
    display: flex;
  }

  &__icons-container {
    display: flex;
    margin-left: auto;
  }
}
