.categoryTitle {
  grid-column: 1/-1;
  display: flex;
  flex-direction: column;
  padding-bottom: 16px;
  gap: 8px;

  @include on-tablet {
    padding-top: 16px;
  }
  
  @include on-desktop {
    padding-top: 16px;
  }

  & > span {
    font-family: Mont, sans-serif;
    font-size: 14px;
    color: $c-secondary;
  }
}
